.recipe {
    border-radius: 20px;
    position: relative;
    height: 550px;
    cursor: pointer;
    > i {
        position: absolute;
        top: 10px;
        right: 10px;
        font-size: 25px;
        color: white;
        transition: color 0.2s;
        z-index: 1;
        &:hover {
            color: var(--red-1);
        }
    }
    i {
        font-size: 20px;
    }
    &:hover {
        img {
            transform: scale(1.1);
        }

        .recipeTitle {
            color: var(--primary);
        }
    }

}

.imageContainer {
    height: 450px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    overflow: hidden;

    img {
        transition: 1s transform;
        width: 100%;
    }
}

.recipeTitle {
    height: 100px;
    transition: 0.2s color;
    border: 1px solid var(--gray-2);
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}